.images {
    /* border-width: 5px;
    border-color: #ffffff;
    border-style: solid; */
    border-radius: 12px;
    height: 200px;
    width: 200px;
    /* padding: 10px; */
    margin: 20px;
    -webkit-transition: box-shadow .2s;
    -webkit-transition-delay: .1s;
    -webkit-transition: -webkit-box-shadow .2s;
    transition: -webkit-box-shadow .2s;
    -o-transition: box-shadow .2s;
    transition: box-shadow .2s;
    transition: box-shadow .2s, -webkit-box-shadow .2s;
    -o-transition-delay: .1s;
       transition-delay: .1s;
    -webkit-box-shadow: 0 9px 10px 0 #7cece9;
            box-shadow: 0 9px 10px 0 #7cece9;
}

.images:hover {
    -webkit-box-shadow: 0 4px 8px 0 #000000, 0 10px 30px 0 #faf737;
            box-shadow: 0 4px 8px 0 #000000, 0 10px 30px 0 #faf737;
}


